import React from 'react';
// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { destroy, get, toggle } from '../../../redux/async/loanTypeAsync';
import { selectedLoanType } from '../../../redux/slices/loanTypeSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Tooltip, Typography } from '@mui/material';
// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import LoanTypeModal from '../modals/LoanTypeModal';
// react-icons
import { FaEdit, FaTrash, FaSlidersH, FaLightbulb } from 'react-icons/fa';
import StatusSwitch from '../shared/StatusSwitch/StatusSwitch';


const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            showCreate={true}
            createEvent={props.create}
            delete={props.delete}
        />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}

export default function LoanTypeDataTable(props) {

    const dispatch = useDispatch();
    const loanTypeRedux = useSelector((state) => state.loanTypes.value);
    const loading = loanTypeRedux.loading;

    const [modalAction, setModalAction] = React.useState("");
    const [showLoanTypeModal, setShowLoanTypeModal] = React.useState(false);
    const toggleLoanTypeModal = () => setShowLoanTypeModal(!showLoanTypeModal);

    const columns = [
        {
            field: 'type', headerName: 'Type', width: 150, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.type.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'rates', headerName: 'Rate', width: 100, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.rates === null ? "..." : `${params.row.rates} %`}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'period', headerName: 'Period', width: 150, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.period === null ? "..." : utilityService.capitalizeFirstLetter(params.row.period)}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'description', headerName: 'Description', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.description === null ? "..." : params.row.description}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'is_active', headerName: 'Status', width: 100, align: 'center', headerAlign: 'center', renderCell: (params) => {

                let status = params.row.is_active;

                return (
                    <Tooltip arrow={true} title={status ? "Active" : "Inactive"} placement="left">
                        <div>
                            <StatusSwitch status={status} onChange={() => dispatch(toggle({ params: params.row }))} />
                        </div>
                    </Tooltip>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaEdit size={18} />}
                    onClick={() => handleEdit(params)}
                    label="Edit"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(1, params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleModal = () => {
        setModalAction("create");
        setShowLoanTypeModal(true);
        dispatch(selectedLoanType({}));
    }

    const handleEdit = async (params) => {
        dispatch(selectedLoanType(params.row));
        setModalAction("edit");
        setShowLoanTypeModal(true);
    }

    const handleDelete = async (row) => {
        let response = await alertService.delete(1);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': [row] }))
        }
    }

    const loadLoanType = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {

        let mounted = false;

        if (!mounted) {
            loadLoanType();
        }

        return () => mounted = true;

    }, [loadLoanType]);

    return (
        <div style={{ height: '100%', width: '100%' }}>
            {showLoanTypeModal && <LoanTypeModal open={showLoanTypeModal} toggle={toggleLoanTypeModal} action={modalAction} />}
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
                <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                Tips: Double-click a row to edit details
            </Typography>
            <div style={{ height: 500, paddingRight: 10 }}>
                <DataGrid
                    rows={loanTypeRedux.loanTypes}
                    onCellDoubleClick={(params) => handleEdit(params)}
                    columns={columns}
                    loading={loading}
                    density="standard"
                    disableSelectionOnClick
                    disableColumnMenu
                    components={{
                        LoadingOverlay: LoadingOverlayOverride,
                        NoRowsOverlay: NoRowOverlayOverride,
                        Toolbar: ToolbarOverride,
                        MoreActionsIcon: ActionsIcon
                    }}
                    componentsProps={{
                        toolbar: {
                            create: () => { handleModal() },
                            delete: () => { handleDelete() },
                        },
                    }}
                />
            </div>
        </div>
    );
}