import React from 'react'

// Redux
import { useDispatch } from 'react-redux';
import { post, put } from '../../../redux/async/accountTypeAsync';

// Modal Components
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
import AccountTypeForm from '../forms/AccountTypeForm';

export default function AccountTypeModal(props) {

    const dispatch = useDispatch();

    const handleSubmit = async (params) => {
        if (props.action === "create") {
            let response = await dispatch(post(params)).unwrap();
            if (!response.error) {
                props.toggle();
            }
        } else {
            let response = await dispatch(put(params)).unwrap();
            if (!response.error) {
                props.toggle();
            }
        }
    }

    return (
        <div>
            <InnerModalDialog open={props.open} title="Account Type" toggle={props.toggle}>
                <AccountTypeForm action={props.action} handleSubmit={handleSubmit} />
            </InnerModalDialog>
        </div>
    )
}
