import React from 'react';
// Utilities 
import utilityService from '../../../services/utils/utilityServices';
import alertService from '../../../services/alert/alertService';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { get, destroy } from '../../../redux/async/accountTypeAsync';
import { selectedAccountType } from '../../../redux/slices/accountTypeSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import Typography from '@mui/material/Typography';
// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import AccountTypeModal from '../modals/AccountTypeModal';
// react-icons
import { FaArchive, FaLightbulb, FaRegEye, FaSlidersH } from 'react-icons/fa';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            showCreate={true}
            createTitle={"Create"}
            createEvent={() => props.create()}
            delete={props.delete}
        />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}


export default function AccountTypeDataTable(props) {

    const dispatch = useDispatch();
    const accountTypeRedux = useSelector((state) => state.accountTypes.value);
    const loading = accountTypeRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [modalAction, setModalAction] = React.useState("");
    const [showAccountTypeModal, setShowAccountTypeModal] = React.useState(false);
    const toggleAccountTypeModal = () => setShowAccountTypeModal(!showAccountTypeModal);

    const columns = [
        {
            field: 'type', headerName: 'Types', width: 200, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.type.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'code', headerName: 'Type Code', hide: true, width: 150, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.code.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'min_interest', headerName: 'Interest Range', width: 150, renderCell: (params) => {

                let min = parseFloat(params.row.min_interest);
                let max = parseFloat(params.row.max_interest);

                let interest_range = `${min}%`;

                if (min < max) {
                    interest_range = `${min}% to ${max}%`;
                }

                return (
                    <div>
                        <span>
                            {interest_range}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'minimum_initial_deposit', headerName: 'Minimum Initial Deposit', width: 200, renderCell: (params) => {

                return (
                    <div>
                        <span>
                            {`PHP ${parseFloat(params.row.minimum_initial_deposit).toFixed(2)}`}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'maintaining_balance', headerName: 'Maintaining Balance', width: 200, renderCell: (params) => {

                return (
                    <div>
                        <span>
                            {`PHP ${parseFloat(params.row.maintaining_balance).toFixed(2)}`}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'monthly_minimum', hide: true, headerName: 'Monthly Minimum', width: 200, renderCell: (params) => {

                return (
                    <div>
                        <span>
                            {`PHP ${parseFloat(params.row.monthly_minimum).toFixed(2)}`}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'description', headerName: 'Descriptions', hide: true, width: 250, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.description === null ? "..." : params.row.description}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'dormancy', headerName: 'Dormancy', width: 120, renderCell: (params) => {

                let dormancy = `${params.row.dormancy} Years`;

                if (params.row?.dormancy === 0) {
                    dormancy = "None";
                }

                return (
                    <div>
                        <span>
                            {dormancy}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'is_withdrawable', headerName: 'Withdrawble', width: 150, headerAlign: 'center', align: 'center', renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.is_withdrawable ? "Yes" : "No"}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', flex: true, type: 'actions', getActions: (params) =>
                [
                    <GridActionsCellItem
                        icon={<FaRegEye color="#00BFA5" />}
                        onClick={() => handleDetails(params)}
                        label="View Details"
                        sx={{
                            color: "#00BFA5",
                            '&:hover': {
                                color: "#00BFA5"
                            }
                        }}
                        showInMenu
                    />,
                    <GridActionsCellItem
                        icon={<FaArchive color='red' />}
                        onClick={() => handleArchive(params)}
                        label="Archive"
                        sx={{
                            color: 'red',
                            '&:hover': {
                                color: "red"
                            }
                        }}
                        showInMenu
                    />
                ]
        }
    ];

    const handleModal = () => {
        setModalAction("create");
        setShowAccountTypeModal(true);
        dispatch(selectedAccountType({}));
    }

    const handleDetails = async (params) => {
        setModalAction("edit");
        dispatch(selectedAccountType(params.row));
        setShowAccountTypeModal(true);
    }

    const handleArchive = async (params) => {

        let response = await alertService.delete(1);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': [params.row.id] }))
        }

    }

    const search = (text) => {
        let filtered = utilityService.filterArrayOfObjects(text, accountTypeRedux.accountTypes);
        setRowData(filtered);
    };

    const loadAccountType = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadAccountType();
        }

        return () => {
            mounted = true;
        }

    }, [loadAccountType]);

    return (
        <div style={{ height: '100%', width: '100%' }}>
            {showAccountTypeModal && <AccountTypeModal open={showAccountTypeModal} toggle={toggleAccountTypeModal} action={modalAction} />}
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
                <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                Tips: Double-click a row to edit details
            </Typography>
            <div style={{ height: 500, paddingRight: 10 }}>s
                <DataGrid
                    rows={rowData.length <= 0 ? accountTypeRedux.accountTypes : rowData}
                    columns={columns}
                    onCellDoubleClick={(params) => handleDetails(params)}
                    loading={loading}
                    density="standard"
                    disableSelectionOnClick
                    disableColumnMenu
                    components={{
                        LoadingOverlay: LoadingOverlayOverride,
                        NoRowsOverlay: NoRowOverlayOverride,
                        Toolbar: ToolbarOverride,
                        MoreActionsIcon: ActionsIcon
                    }}
                    componentsProps={{
                        toolbar: {
                            create: () => handleModal(),
                            onChange: (event) => search(event.target.value),
                        },
                    }}
                />
            </div>
        </div>
    );
}