import React from 'react';
// Utilities 
import alertService from '../../../services/alert/alertService';
// Redux
import { useSelector, useDispatch } from 'react-redux';
import { destroy, get } from '../../../redux/async/employmentTypeAsync';
import { selectedEmploymentType } from '../../../redux/slices/employmentTypeSlice';
// MUI Components
import { DataGrid, GridActionsCellItem } from '@mui/x-data-grid';
import { Typography } from '@mui/material';
// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import Toolbar from '../grid/Toolbar';
import EmploymentTypeModal from '../modals/EmploymentTypeModal';
// react-icons
import { FaEdit, FaTrash, FaLightbulb, FaSlidersH } from 'react-icons/fa';


const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}

const ToolbarOverride = (props) => {
    return (
        <Toolbar
            showCreate={true}
            createTitle={"Create"}
            createEvent={() => props.create()}
            delete={props.delete}
        />
    );
}

const ActionsIcon = () => {
    return <FaSlidersH />
}

export default function EmploymentTypeDataTable(props) {

    const dispatch = useDispatch();
    const employmentTypeRedux = useSelector((state) => state.employmentTypes.value);
    const loading = employmentTypeRedux.loading;

    const [modalAction, setModalAction] = React.useState("");
    const [showEmploymentTypeModal, setShowEmploymentTypeModal] = React.useState(false);
    const toggleEmploymentTypeModal = () => setShowEmploymentTypeModal(!showEmploymentTypeModal);

    const columns = [
        {
            field: 'type', headerName: 'Type', width: 350, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.type.toUpperCase()}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'description', headerName: 'Description', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.description === null ? "..." : params.row.description}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'actions', headerName: 'Options', type: 'actions', getActions: (params) => [
                <GridActionsCellItem
                    icon={<FaEdit size={18} />}
                    onClick={() => handleEdit(params)}
                    label="Edit"
                    sx={{
                        '&:hover': {
                            color: "#00BFA5"
                        }
                    }}
                    showInMenu
                />,
                <GridActionsCellItem
                    icon={<FaTrash size={15} />}
                    onClick={() => handleDelete(params.row.id)}
                    label="Delete"
                    sx={{
                        '&:hover': {
                            color: "#E45570"
                        }
                    }}
                    showInMenu
                />
            ]
        },
    ];

    const handleEdit = async (params) => {
        dispatch(selectedEmploymentType(params.row));
        setModalAction("edit");
        setShowEmploymentTypeModal(true);
    }

    const handleDelete = async (row) => {
        let response = await alertService.delete(1);
        if (response.isConfirmed) {
            dispatch(destroy({ 'ids': [row] }))
        }
    }

    const loadEmploymentType = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadEmploymentType();
        }

        return () => mounted = true;
    }, [loadEmploymentType]);

    return (
        <div style={{ height: '100%', width: '100%' }}>
            {showEmploymentTypeModal && <EmploymentTypeModal open={showEmploymentTypeModal} toggle={toggleEmploymentTypeModal} action={modalAction} />}
            <Typography variant="subtitle2" sx={{ mb: 1 }}>
                <FaLightbulb size={14} style={{ marginRight: 2, color: "#00BFA5" }} />
                Tips: Double-click a row to edit details
            </Typography>
            <div style={{ height: 500, paddingRight: 10 }}>
                <DataGrid
                    rows={employmentTypeRedux.employmentTypes}
                    columns={columns}
                    loading={loading}
                    onCellDoubleClick={(params) => handleEdit(params)}
                    density="standard"
                    disableSelectionOnClick
                    disableColumnMenu
                    components={{
                        LoadingOverlay: LoadingOverlayOverride,
                        NoRowsOverlay: NoRowOverlayOverride,
                        Toolbar: ToolbarOverride,
                        MoreActionsIcon: ActionsIcon
                    }}
                    componentsProps={{
                        toolbar: {
                            create: () => { setModalAction("create"); setShowEmploymentTypeModal(true); dispatch(selectedEmploymentType({})); },
                            delete: () => { handleDelete() },
                        },
                    }}
                />
            </div>
        </div>
    );
}