
import React from 'react';
import utilityServices from '../../../services/utils/utilityServices';
// redux
import { useSelector } from 'react-redux';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import LoadingButton from '@mui/lab/LoadingButton';
import Switch from '@mui/material/Switch';
import Box from '@mui/material/Box';

// Page Components
import YearOrMonth from './fields/YearOrMonth';
import YesOrNoSelect from './fields/YesOrNoSelect';
// react-icons
import { FaCheck, FaPercent } from 'react-icons/fa';
import { IconButton, Typography } from '@mui/material';
import utilityService from '../../../services/utils/utilityServices';

export default function AccountTypeForm(props) {

    const accountTypeRedux = useSelector((state) => state.accountTypes.value);
    const selectedAccountType = accountTypeRedux.selectedAccountType;

    const [isEdit, setIsEdit] = React.useState(false);
    const [accountType, setAccountType] = React.useState("");
    const [code, setCode] = React.useState("");
    const [description, setDescription] = React.useState("");
    const [minInterest, setMinInterest] = React.useState("");
    const [maxInterest, setMaxInterest] = React.useState("");
    const [minInitialDeposit, setMinInitialDeposit] = React.useState("");
    const [monthlyMinimum, setMonthlyMinimum] = React.useState("");
    const [maintainingBalance, setMaintainingBalance] = React.useState("");
    const [dormancy, setDormancy] = React.useState("");
    const [yearOrMonth, setYearOrMonth] = React.useState("");
    const [isWithdrawble, setIsWithdrawble] = React.useState("");
    const [canRollover, setCanRollover] = React.useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();

        let data = {
            "data": {
                type: accountType,
                code: code,
                description: description,
                min_interest: minInterest,
                max_interest: maxInterest,
                minimum_initial_deposit: minInitialDeposit,
                maintaining_balance: maintainingBalance,
                monthly_minimum: monthlyMinimum,
                description: description,
                dormancy: `${dormancy} ${yearOrMonth}`,
                is_withdrawable: isWithdrawble,
                can_rollover: canRollover,
            },
            "params": accountTypeRedux.selectedAccountType
        }

        props.handleSubmit(data);
    }

    React.useEffect(() => {

        if (selectedAccountType !== null && selectedAccountType !== undefined && Object.keys(selectedAccountType).length > 0) {  

            setAccountType(utilityService.capitalizeFirstLetter(selectedAccountType.type));
            setCode(selectedAccountType.code);
            setMinInterest(selectedAccountType.min_interest);
            setMaxInterest(selectedAccountType.max_interest);
            setMinInitialDeposit(parseFloat(selectedAccountType.minimum_initial_deposit).toFixed(2));
            setMonthlyMinimum(parseFloat(selectedAccountType.monthly_minimum).toFixed(2));
            setMaintainingBalance(parseFloat(selectedAccountType.maintaining_balance).toFixed(2));
            setDormancy(parseInt(selectedAccountType.dormancy));
            setYearOrMonth("Years");
            setDescription(selectedAccountType.description);
            setIsWithdrawble(selectedAccountType.is_withdrawable === 1 ? true : false);
            setCanRollover(selectedAccountType.can_rollover === 1 ? true : false);
            
        }

        if (props.action === "create") {
            setIsEdit(true);
        }

    }, [selectedAccountType])

    return (
        <div style={{ minWidth: 200 }}>
            <Paper sx={{ p: 0, m: 0, mt: 1 }} elevation={0}>
                {props.action !== "create" &&
                    <Box sx={{ m: 1, ml: -2, mb: 2 }}>
                        <Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} /> Edit
                    </Box>
                }
                <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
                    <Grid container spacing={2} >
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setAccountType(e.target.value)}
                                value={accountType}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="accountstaType"
                                label="Account Type"
                                name="accountType"
                                inputProps={{
                                    readOnly: !isEdit
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setCode(e.target.value)}
                                value={code}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="code"
                                label="Account Type Code"
                                name="code"
                                inputProps={{
                                    readOnly: !isEdit
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <YearOrMonth fullWidth={true} label="Dormancy" value={dormancy} setDormancy={setDormancy} yearOrMonth={yearOrMonth} setYearOrMonth={setYearOrMonth} />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setMinInterest(e.target.value)}
                                value={minInterest}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="minInterest"
                                label="Minimum Interest"
                                name="minInterest"
                                type='number'
                                inputProps={{
                                    min: 0.001,
                                    step: 0.001
                                }}
                                InputProps={{
                                    endAdornment: (
                                        <IconButton disableFocusRipple disableRipple>
                                            <FaPercent size={12} />
                                        </IconButton>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setMaxInterest(e.target.value)}
                                value={maxInterest}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="minInterest"
                                label="Maximum Interest"
                                name="minInterest"
                                type='number'
                                inputProps={{
                                    min: 0.001,
                                    step: 0.001
                                }}
                                InputProps={{
                                    endAdornment: (
                                        <IconButton disableFocusRipple disableRipple>
                                            <FaPercent size={12} />
                                        </IconButton>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                type="number"
                                onChange={(e) => setMinInitialDeposit(e.target.value)}
                                value={minInitialDeposit}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="minInitialDeposit"
                                label="Minimum Initial Deposit"
                                name="minInitialDeposit"
                                inputProps={{
                                    min: 1,
                                    step: 0.1
                                }}
                                InputProps={{
                                    startAdornment: (
                                        <IconButton disableFocusRipple disableRipple sx={{ pl: 0 }}>
                                            <Typography sx={{ minWidth: 10 }}>
                                                PHP
                                            </Typography>
                                        </IconButton>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                type="number"
                                onChange={(e) => setMonthlyMinimum(e.target.value)}
                                value={monthlyMinimum}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="monthlyMinimum"
                                label="Monthly Minimum"
                                name="monthlyMinimum"
                                inputProps={{
                                    min: 0,
                                    step: 0.1
                                }}
                                InputProps={{
                                    startAdornment: (
                                        <IconButton disableFocusRipple disableRipple sx={{ pl: 0 }}>
                                            <Typography sx={{ minWidth: 10 }}>
                                                PHP
                                            </Typography>
                                        </IconButton>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                type="number"
                                onChange={(e) => setMaintainingBalance(e.target.value)}
                                value={maintainingBalance}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="maintainingBalance"
                                label="Maintaining Balance"
                                name="maintainingBalance"
                                inputProps={{
                                    min: 0,
                                    step: 0.1
                                }}
                                InputProps={{
                                    startAdornment: (
                                        <IconButton disableFocusRipple disableRipple sx={{ pl: 0 }}>
                                            <Typography sx={{ minWidth: 10 }}>
                                                PHP
                                            </Typography>
                                        </IconButton>
                                    )
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <YesOrNoSelect label="Is this withdrawble ?" yesOrNo={isWithdrawble} setYesOrNo={setIsWithdrawble} />
                        </Grid>
                        <Grid item xs={6}>
                            <YesOrNoSelect label="Can this be rollover ?" yesOrNo={canRollover} setYesOrNo={setCanRollover} />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                onChange={(e) => setDescription(e.target.value)}
                                value={description}
                                variant="outlined"
                                size="small"
                                multiline
                                maxRows={3}
                                minRows={3}
                                required
                                fullWidth
                                id="description"
                                label="Description"
                                name="description"
                            />
                        </Grid>
                        <Grid item xs={12}>
                            {
                                isEdit &&
                                <LoadingButton
                                    type="submit"
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    loading={accountTypeRedux.loading}
                                >
                                    {utilityServices.capitalizeFirstLetter(props.action)}
                                    <FaCheck style={{ marginLeft: 4 }} />
                                </LoadingButton>
                            }
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </div >
    )
}
